@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'IBM Plex Serif', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nav {
  position: fixed;
  display: flex;
  flex-direction: row;
  width: 100vw;
  justify-content: flex-end;
}

.nav .actions {
  padding: 16px;
  display: flex;
  flex-direction: row;
}

.nav .actions .user-info {
  padding: 8px 32px;
  color: #1e3772;
}
/*# sourceMappingURL=nav.style.css.map */
.container--home {
  display: flex;
  flex-direction: column;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

@media (min-width: 840px) {
  .container--home {
    flex-direction: row;
    justify-content: space-between;
  }
}

.container--home #bg {
  -webkit-clip-path: circle(50% at 92% 6%);
          clip-path: circle(50% at 92% 6%);
  background-color: #d2e3ff;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}

.container--home .welcome-home {
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 24px;
}

.container--home .welcome-home .intro {
  font-size: 32px;
  font-weight: 600;
  line-height: 40px;
  margin-bottom: 15px;
}

.container--home .welcome-home .intro span {
  font-size: 40px;
  font-weight: 800;
  letter-spacing: 2px;
  color: #407bff;
}

.container--home .welcome-home .description {
  font-size: 18px;
  line-height: 32px;
  letter-spacing: 1px;
  margin-bottom: 40px;
  font-weight: 300;
}

.container--home .banner-home {
  margin-top: 90px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (min-width: 840px) {
  .container--home .banner-home {
    padding: 24px;
  }
}

.container--home .banner-home img {
  height: 250px;
}

@media (min-width: 840px) {
  .container--home .banner-home img {
    height: 500px;
  }
}
/*# sourceMappingURL=home.style.css.map */
.link {
  cursor: pointer;
  border: none;
  outline: none;
  background-color: white;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.link--sign-in {
  cursor: pointer;
  padding: 10px 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #10aa18;
  border: none;
  border-radius: 8px;
  outline: none;
  font-size: 16px;
  font-family: 'IBM Plex Serif', serif;
  letter-spacing: 1px;
  font-weight: 600;
  line-height: 24px;
  color: white;
}

.link--sign-in:focus {
  background-color: #10aa18;
  border: none;
  border-radius: 8px;
  outline: none;
}

.link--sign-in:active {
  background-color: #10aa18;
  border: none;
  border-radius: 8px;
  outline: none;
}

.link--sign-in:hover {
  background-color: #91d493;
  border: none;
  border-radius: 8px;
  outline: none;
}

.link--nav {
  background-color: rgba(255, 255, 255, 0);
  color: #00225c;
}
/*# sourceMappingURL=link.style.css.map */
